<template>
  <div>
    <a-card>
      <a-row :gutter="20">
        <a-col :span="6">
          <a-input-search
            v-model="queryParam.username"
            placeholder=""
            enter-button
            allowClear
            @search="getUserList"
          />
        </a-col>
        <a-col :span="2">
          <a-button icon="plus" type="primary" @click="addUserVisible = true"
            ></a-button
          >
        </a-col>
      </a-row>
      <a-table
        rowKey="username"
        :columns="columns"
        :pagination="pagination"
        :dataSource="userlist"
        @change="handleTableChange"
        bordered
      >
        <span slot="role" slot-scope="role">{{
          role == 1 ? "" : ""
        }}</span>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button
              type="dashed"
              style="margin-right: 8px"
              icon="edit"
              @click="editUser(data.ID)"
              ></a-button
            >
            <a-button
              icon="minus-circle"
              type="dashed"
              @click="ChangePassword(data.ID)"
              ></a-button
            >
            <a-popconfirm
              title="  "
              placement="left"
              ok-text=""
              cancel-text=""
              @confirm="deleteUser(data.ID)"
              @keyup.enter.native="deleteUser(data.ID)"
              @cancel="cancel"
            >
              <a-button icon="delete" style="margin: 0 8px" type="danger"
                ></a-button
              >
              <!-- @click="deleteUser(data.ID)" -->
            </a-popconfirm>
            <a-button
              icon="info-circle"
              type="link"
              @click="getPersonalInfo(data.ID)"
              ></a-button
            >
          </div>
        </template>
      </a-table>
    </a-card>

    <!--  ------------------------------------------------------------------------ -->
    <a-modal
      closable
      title=""
      :visible="changePasswordVisible"
      width="20%"
      @ok="changePasswordOk"
      @cancel="changePasswordCancel"
      destroyOnClose
    >
      <a-form-model
        :model="changePassword"
        :rules="changePasswordRules"
        ref="changePasswordRef"
      >
        <a-form-model-item has-feedback label="" prop="password">
          <a-input-password
            v-model="changePassword.password"
          ></a-input-password>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="checkpass">
          <a-input-password
            v-model="changePassword.checkpass"
          ></a-input-password>
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!-- ---------------------------------------------------------------------------- -->
    <a-modal
      closable
      width="25%"
      destroyOnClose
      title=""
      :visible="addUserVisible"
      @ok="addUserOk"
      @keyup.enter.native="addUserOk"
      @cancel="addUserCancel"
    >
      <a-form-model :model="newUser" :rules="addUserRules" ref="addUserRef">
        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label=""
          prop="username"
        >
          <a-input v-model="newUser.username"></a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label=""
          prop="password"
        >
          <a-input-password v-model="newUser.password"></a-input-password>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label=""
          prop="checkpass"
        >
          <a-input-password v-model="newUser.checkpass"></a-input-password>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label=""
          prop="phone"
        >
          <a-input v-model="newUser.phone"></a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label=""
          prop="email"
        >
          <a-input v-model="newUser.email"></a-input>
        </a-form-model-item>

        <a-form-model-item label="" prop="role">
          <!-- <a-select defauleValue="2" style="120px" @change="adminChange">
            <a-select-option key="1" value="1"></a-select-option>
            <a-select-option key="2" value="2"></a-select-option>
          </a-select> -->
          <a-switch
            checked-children=""
            un-checked-children=""
            @change="adminChange"
          />
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!-- ---------------------------------------------------------------------------- -->
    <a-modal
      closable
      width="25%"
      title=""
      :visible="editUserVisible"
      @ok="editUserOk"
      @keyup.enter.native="editUserOk"
      @cancel="editUserCancel"
    >
      <a-form-model :model="userInfo" :rules="userRules" ref="addUserRef">
        <a-form-model-item has-feedback label="" prop="username">
          <a-input v-model="userInfo.username"></a-input>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="phone">
          <a-input v-model="userInfo.phone"></a-input>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="email">
          <a-input v-model="userInfo.email"></a-input>
        </a-form-model-item>
        <a-form-model-item label="">
          <!-- <a-select defauleValue="2" style="120px" @change="adminChange">
            <a-select-option key="1" value="1"></a-select-option>
            <a-select-option key="2" value="2"></a-select-option>
          </a-select> -->
          <a-switch
            checked-children=""
            un-checked-children=""
            @change="adminChange"
          />
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!--  -------------------------------------------------------------------- -->
    <a-modal
      closable
      width="30%"
      title=""
      :visible="personalVisible"
      @ok="personalOk"
      @keyup.enter.native="personalOk"
      @cancel="personalCancel"
    >
      <a-form-model
        :model="personalInfo"
        :rules="personalRules"
        ref="personalRef"
      >
        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label=""
          prop="name"
        >
          <a-input v-model="personalInfo.name"></a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label=""
          prop="desc"
        >
          <a-input v-model="personalInfo.desc"></a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label="QQ"
          prop="qq_chat"
        >
          <a-input v-model="personalInfo.qq_chat"></a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          has-feedback
          label=""
          prop="wechat"
        >
          <a-input v-model="personalInfo.wechat"></a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          style="margin: 0 auto"
          label=""
          prop="avatar"
        >
          <a-upload
            name="file"
            listType="picture"
            :action="upUrl"
            :headers="headers"
            @change="avatarChange"
          >
            <a-button> <a-icon type="upload" />  </a-button>
            <br />
            <template v-if="personalInfo.avatar">
              <img
                :src="personalInfo.avatar"
                style="width: 120px; height: 120px; margin-top: 8px"
              />
            </template>
          </a-upload>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
import { Url } from "../../plugin/http";
const columns = [
  {
    title: "ID",
    dataIndex: "ID",
    width: "8%",
    key: "id",
    align: "center",
  },
  {
    title: "",
    dataIndex: "username",
    width: "16%",
    key: "username",
    align: "center",
  },
  {
    title: "",
    dataIndex: "phone",
    width: "16%",
    key: "phone",
    align: "center",
  },
  {
    title: "",
    dataIndex: "email",
    width: "20%",
    key: "email",
    align: "center",
  },
  {
    title: "",
    dataIndex: "role",
    width: "10%",
    key: "role",
    align: "center",
    scopedSlots: { customRender: "role" },
  },
  {
    title: "",
    width: "30%",
    key: "action",
    align: "center",
    scopedSlots: { customRender: "action" },
  },
];
export default {
  data() {
    return {
      upUrl: Url + "/upload",
      headers: {}, // 
      userlist: [],
      columns,
      labelCol: { span: 6 },
      wrapperCol: { span: 14 },
      pagination: {
        pageSizeOptions: ["5", "10", "20"],
        pageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `${total}`,
      },
      queryParam: {
        username: "",
        pagesize: 5,
        pagenum: 1,
      },
      userInfo: {
        // 
        id: 0,
        username: "",
        password: "",
        checkpass: "",
        phone: "",
        email: "",
        role: 2,
      },
      personalInfo: {
        id: 0,
        name: "",
        desc: "",
        qq_chat: "",
        wechat: "",
        avatar: "",
      },
      newUser: {
        // 
        id: 0,
        username: "",
        password: "",
        checkpass: "",
        phone: "",
        email: "",
        role: 2,
      },
      changePassword: {
        id: 0,
        password: "",
        checkPass: "",
      },
      visible: false, // 
      addUserVisible: false, // 
      personalRules: {
        // 
        name: [
          {
            validator: (rule, value, callback) => {
              if (this.personalInfo.name == "") {
                callback(new Error(""));
              }
              if (
                [...this.personalInfo.name].length < 1 ||
                [...this.personalInfo.name].length > 12
              ) {
                callback(new Error(" 1 ~ 12 "));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
        desc: [
          {
            validator: (rule, value, callback) => {
              if (this.personalInfo.desc == "") {
                callback(new Error(""));
              }
              if (
                [...this.personalInfo.desc].length < 1 ||
                [...this.personalInfo.desc].length > 50
              ) {
                callback(new Error(" 1 ~ 50 "));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
        qq_chat: [
          {
            validator: (rule, value, callback) => {
              if (this.personalInfo.qq_chat == "") {
                callback(new Error("QQ"));
              }
              if (
                [...this.personalInfo.qq_chat].length < 5 ||
                [...this.personalInfo.qq_chat].length > 10
              ) {
                callback(new Error("QQ5 ~ 10 "));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
        wechat: [
          {
            validator: (rule, value, callback) => {
              if (this.personalInfo.wechat == "") {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
      },
      userRules: {
        // 
        username: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.username == "") {
                callback(new Error(""));
              }
              if (
                [...this.userInfo.username].length < 4 ||
                [...this.userInfo.username].length > 12
              ) {
                callback(new Error(" 4 ~ 12 "));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.password == "") {
                callback(new Error(""));
              }
              if (
                [...this.userInfo.password].length < 6 ||
                [...this.userInfo.password].length > 20
              ) {
                callback(new Error(" 6 ~ 20 "));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
        checkpass: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.checkpass == "") {
                callback(new Error(""));
              }
              if (this.userInfo.password !== this.userInfo.checkpass) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
        phone: [
          {
            validator: (rule, value, callback) => {
              var reg = /^1(3|4|5|6|7|8)\d{9}$/;
              if (!reg.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
        email: [
          {
            validator: (rule, value, callback) => {
              var reg = /^([a-zA-Z]|[0-9])(\w|-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
              if (!reg.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: ["blur", "change"],
          },
        ],
      },

      addUserRules: {
        // 
        username: [
          {
            validator: (rule, value, callback) => {
              if (this.newUser.username == "") {
                callback(new Error(""));
              }
              if (
                [...this.newUser.username].length < 4 ||
                [...this.newUser.username].length > 12
              ) {
                callback(new Error(" 4 ~ 12 "));
              } else {
                callback();
              }
            },
            trigger: ["blur", "change"],
          },
        ],
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.newUser.password == "") {
                callback(new Error(""));
              }
              if (
                [...this.newUser.password].length < 6 ||
                [...this.newUser.password].length > 20
              ) {
                callback(new Error(" 6 ~ 20 "));
              } else {
                callback();
              }
            },
            trigger: ["blur", "change"],
          },
        ],
        checkpass: [
          {
            validator: (rule, value, callback) => {
              if (this.newUser.checkpass == "") {
                callback(new Error(""));
              }
              if (this.newUser.password !== this.newUser.checkpass) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: ["blur", "change"],
          },
        ],
        phone: [
          {
            validator: (rule, value, callback) => {
              var reg = /^1(3|4|5|6|7|8)\d{9}$/;
              if (!reg.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: ["blur", "change"],
          },
        ],
        email: [
          {
            validator: (rule, value, callback) => {
              var reg = /^([a-zA-Z]|[0-9])(\w|-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
              if (!reg.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: ["blur", "change"],
          },
        ],
      },
      changePasswordRules: {
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.changePassword.password == "") {
                callback(new Error(""));
              }
              if (
                [...this.changePassword.password].length < 6 ||
                [...this.changePassword.password].length > 20
              ) {
                callback(new Error("620"));
              } else {
                callback();
              }
            },
            trigger: ["blur", "change"],
          },
        ],
        checkpass: [
          {
            validator: (rule, value, callback) => {
              if (this.changePassword.checkpass == "") {
                callback(new Error(""));
              }
              if (
                this.changePassword.password !== this.changePassword.checkpass
              ) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: ["blur", "change"],
          },
        ],
      },
      changePasswordVisible: false, // 
      editUserVisible: false, // 
      personalVisible: false, // 
    };
  },
  created() {
    this.getUserList();
    this.headers = {
      Authorization: `Bearer ${window.sessionStorage.getItem("token")}`,
    };
  },
  methods: {
    // 
    async getUserList() {
      const { data: res } = await this.$http.get("users", {
        params: {
          username: this.queryParam.username,
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
        },
      });
      if (res.status != 200) return this.$message.error(res.message);
      this.userlist = res.data;
      this.pagination.total = res.total;
      console.log(res);
    },
    // 
    handleTableChange(pagination, filters, sorter) {
      var pager = { ...this.pagination };
      pager.current = pagination.current;
      pager.pageSize = pagination.pageSize;
      this.queryParam.pagesize = pagination.pageSize;
      this.queryParam.pagenum = pagination.current;

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1;
        pager.current = 1;
      }
      this.pagination = pager;
      this.getUserList();
    },

    // 
    // async deleteUser(id) {
    //   const res = await this.$http.delete(`user/${id}`);
    //       if (res.status != 200) return this.$message.error(res.message);
    //       this.$message.success("");
    //       this.getUserList();
    //   // console.log(res)
    // },
    async deleteUser(id) {
      const res = await this.$http.delete(`user/${id}`);
      if (res.status != 200) return this.$message.error(res.message);
      this.getUserList();
      this.$message.success("");
    },
    cancel() {
      this.$message.error("");
    },

    // ---------------------------------------------------------------------------
    addUserOk() {
      this.$refs.addUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        const { data: res } = await this.$http.post("user/add", {
          username: this.newUser.username,
          password: this.newUser.password,
          phone: parseInt(this.newUser.phone),
          email: this.newUser.email,
          role: this.newUser.role,
        });
        if (res.status !== 200) return this.$message.error(res.message);

        this.addUserVisible = false;
        this.$message.success("");
        this.getUserList();
      });
    },
    addUserCancel() {
      this.$refs.addUserRef.resetFields();
      this.addUserVisible = false;
      this.$message.info("");
    },
    adminChange(checked) {
      //this.userInfo.role = Number(value);
      // console.log(this.userInfo.role)
      //console.log(checked);
      if (checked) {
        this.userInfo.role = Number(1);
      } else {
        this.userInfo.role = Number(2);
      }
    },

    // ------------------------------------------------------------------------------------
    async editUser(id) {
      this.editUserVisible = true;
      const { data: res } = await this.$http.get(`user/${id}`);
      this.userInfo = res.data;
      this.userInfo.id = id;
    },
    editUserOk() {
      this.$refs.addUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        const { data: res } = await this.$http.put(`user/${this.userInfo.id}`, {
          username: this.userInfo.username,
          phone: parseInt(this.userInfo.phone),
          email: this.userInfo.email,
          role: this.userInfo.role,
        });
        if (res.status !== 200) return this.$message.error(res.message);

        this.editUserVisible = false;
        this.$message.success("");
        this.getUserList();
      });
    },
    editUserCancel() {
      this.$refs.addUserRef.resetFields();
      this.editUserVisible = false;
      this.$message.info("");
    },

    //  ------------------------------------------------------------------------
    // 
    async getPersonalInfo(id) {
      this.personalVisible = true;
      const { data: res } = await this.$http.get(`personal/${id}`);
      if (res.status !== 200) return this.$message.error(res.message);
      this.personalInfo = res.data;
      this.personalInfo.id = id;
      //   this.bookInfo.id = res.data.ID;
      //console.log(this.personalInfo);
      //console.log(res);
    },
    // 
    async personalOk(id) {
      this.$refs.personalRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        const { data: res } = await this.$http.put(
          `personal/${this.personalInfo.id}`,
          {
            name: this.personalInfo.name,
            desc: this.personalInfo.desc,
            qq_chat: this.personalInfo.qq_chat,
            wechat: this.personalInfo.wechat,
            avatar: this.personalInfo.avatar,
          }
        );
        if (res.status !== 200) return this.$message.error(res.message);
        this.personalVisible = false;
        this.$message.success(``);
        this.getUserList();
      });
    },
    // 
    personalCancel() {
      this.$refs.personalRef.resetFields();
      this.personalVisible = false;
      this.$message.info("");
    },
    // 
    avatarChange(info) {
      if (info.file.status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (info.file.status === "done") {
        this.$message.success(``);
        const imgUrl = info.file.response.url;
        this.personalInfo.avatar = imgUrl;
      } else if (info.file.status === "error") {
        this.$message.error(``);
      }
    },

    //  -----------------------------------------------------------------------------
    async ChangePassword(id) {
      this.changePasswordVisible = true;
      const { data: res } = await this.$http.get(`user/${id}`);
      this.changePassword.id = id;
    },
    changePasswordOk() {
      this.$refs.changePasswordRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        const { data: res } = await this.$http.put(
          `changepw/${this.changePassword.id}`,
          {
            password: this.changePassword.password,
          }
        );
        if (res.status != 200) return this.$message.error(res.message);
        this.changePasswordVisible = false;
        this.$message.success("");
        this.getUserList();
      });
    },
    changePasswordCancel() {
      this.$refs.changePasswordRef.resetFields();
      this.changePasswordVisible = false;
      this.$message.info("");
    },
  },
};
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>