<template>
  <div id="addbook">
    <a-card>
      <a-divider
        ><h3>{{ id ? "" : "" }}</h3></a-divider
      >
      <a-form-model
        :model="bookInfo"
        ref="bookInfoRef"
        :rules="bookInfoRules"
        :hideRequiredMark="true"
      >
        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          label=" "
          prop="title"
        >
          <a-input placeholder="" v-model="bookInfo.title">
            <a-icon slot="prefix" type="robot" />
            <a-tooltip slot="suffix" title="">
              <a-icon type="info-circle" style="color: rgba(0, 0, 0, 0.45)" />
            </a-tooltip>
          </a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          label=" "
          prop="author"
        >
          <a-input placeholder="" v-model="bookInfo.author">
            <a-icon slot="prefix" type="user" />
            <a-tooltip slot="suffix" title="">
              <a-icon type="info-circle" style="color: rgba(0, 0, 0, 0.45)" />
            </a-tooltip>
          </a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          label=" "
          prop="price"
        >
          <a-input
            placeholder=""
            prefix=""
            suffix="RMB"
            v-model="bookInfo.price"
          ></a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          label=" "
          prop="cid"
        >
          <a-select
            placeholder=""
            v-model="bookInfo.cid"
            @change="cateChange"
            :defaultFileList="fileList"
          >
            <a-select-option
              v-for="item in Catelist"
              :key="item.id"
              :value="item.id"
              >{{ item.name }}</a-select-option
            >
          </a-select>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          label=" "
          prop="Desc"
        >
          <a-input
            placeholder=""
            type="textarea"
            v-model="bookInfo.Desc"
          >
          </a-input>
        </a-form-model-item>

        <a-form-model-item
          :label-col="labelCol"
          :wrapper-col="wrapperCol"
          style="width: 760px; margin: 0 auto"
          label=""
          prop="img"
        >
          <a-upload
            name="file"
            listType="picture"
            :action="upUrl"
            :headers="headers"
            @change="upChange"
          >
            <a-button> <a-icon type="upload" />  </a-button>
            <br />
            <template v-if="id">
              <img :src="bookInfo.img" style="width: 120px; height: 120px" />
            </template>
          </a-upload>
        </a-form-model-item>

        <a-divider></a-divider>

        <a-form-model-item >
          <a-button
            type="danger"
            style="margin-right: 15px"
            @click="bookOk(bookInfo.id)"
            >{{ bookInfo.id ? "" : "" }}</a-button
          >
          <a-button type="primary" @click="addCancel"></a-button>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
import { Url } from "../../plugin/http";
export default {
  props: ["id"],
  data() {
    return {
      labelCol: { span: 9 },
      wrapperCol: { span: 6 },
      bookInfo: {
        id: 0,
        title: "",
        author: "",
        cid: undefined,
        desc: "",
        price: "",
        img: "",
      },
      Catelist: [],
      upUrl: Url + "/upload",
      headers: {}, // 
      fileList: [], // 
      bookInfoRules: {
        // 
        title: [{ required: true, message: "", trigger: "blur" }],
        author: [
          { required: true, message: "", trigger: "blur" },
        ],
        Desc: [
          { required: true, message: "", trigger: "blur" },
          { max: 120, message: "120", trigger: "change" },
        ],
        cid: [{ required: true, message: "", trigger: "blur" }],
        img: [{ required: true, message: "", trigger: "blur" }],
        price: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  // 
  created() {
    this.getCateList();
    this.headers = {
      Authorization: `Bearer ${window.sessionStorage.getItem("token")}`,
    };
    if (this.id) {
      this.getBookInfo(this.id);
    }
  },
  methods: {
    // 
    async getBookInfo(id) {
      const { data: res } = await this.$http.get(`/book/info/${id}`);
      if (res.status !== 200) return this.$message.error(res.message);
      this.bookInfo = res.data;
      this.bookInfo.id = res.data.ID;
      // console.log(res);
    },
    // 
    async getCateList() {
      const { data: res } = await this.$http.get("/category");
      if (res.status !== 200) return this.$message.error(res.message);
      this.Catelist = res.data;
    },
    // 
    cateChange(value) {
      this.bookInfo.cid = value;
    },
    // 
    upChange(info) {
      if (info.file.status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (info.file.status === "done") {
        this.$message.success(``);
        const imgUrl = info.file.response.url;
        this.bookInfo.img = imgUrl;
      } else if (info.file.status === "error") {
        this.$message.error(``);
      }
    },

    // &&
    bookOk(id) {
      this.$refs.bookInfoRef.validate(async (valid) => {
        if (id === 0) {
          const { data: res } = await this.$http.post(
            "/book/add",
            this.bookInfo
          );
          if (res.status !== 200) return this.$message.error(res.message);
          this.$router.push("/booklist");
          this.$message.success("");
        } else {
          const { data: res } = await this.$http.put(
            `/book/${id}`,
            this.bookInfo
          );
          if (res.status !== 200) return this.$message.error(res.message);
          // this.$router.push("/admin/booklist");
          this.$router.push("/booklist");
          this.$message.success("");
        }
      });
    },
    // &&
    addCancel() {
      this.$refs.bookInfoRef.resetFields();
    },
  },
};
</script>