<template>
  <div>
    <a-card>
      <a-row :gutter="20">
        <a-col :span="4">
          <a-button icon="plus" type="primary" @click="addCateVisible = true"
            ></a-button
          >
        </a-col>
      </a-row>
      <a-table
        rowKey="id"
        :columns="columns"
        :pagination="pagination"
        :dataSource="Catelist"
        @change="handleTableChange"
        bordered
      >
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="primary" icon="edit" @click="editCate(data.id)"
              ></a-button
            >
            <a-popconfirm
              title="  "
              placement="left"
              ok-text=""
              cancel-text=""
              @confirm="deleteCate(data.id)"
              @keyup.enter.native="deleteCate(data.id)"
              @cancel="cancel"
            >
              <a-button icon="delete" style="margin: 0 20px" type="danger"
                ></a-button
              >
              <!-- @click="deleteCate(data.ID)" -->
            </a-popconfirm>
          </div>
        </template>
      </a-table>
    </a-card>

    <!-- ---------------------------------------------------------------------------- -->
    <a-modal
      closable
      width="30%"
      destroyOnClose
      title=""
      :visible="addCateVisible"
      @ok="addCateOk"
      @keyup.enter.native="addCateOk"
      @cancel="addCateCancel"
    >
      <a-form-model :model="newCate" :rules="addCateRules" ref="addCateRef">
        <a-form-model-item has-feedback label="" prop="name">
          <a-input v-model="newCate.name"></a-input>
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!-- ---------------------------------------------------------------------------- -->
    <a-modal
      closable
      destroyOnClose
      width="30%"
      title=""
      :visible="editCateVisible"
      @ok="editCateOk"
      @keyup.enter.native="editCateOk"
      @cancel="editCateCancel"
    >
      <a-form-model :model="CateInfo" :rules="CateRules" ref="addCateRef">
        <a-form-model-item has-feedback label="" prop="name">
          <a-input v-model="CateInfo.name"></a-input>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
const columns = [
  {
    title: "ID",
    dataIndex: "id",
    width: "30%",
    key: "id",
    align: "center",
  },
  {
    title: "",
    dataIndex: "name",
    width: "30%",
    key: "name",
    align: "center",
  },
  {
    title: "",
    width: "40%",
    key: "action",
    align: "center",
    scopedSlots: { customRender: "action" },
  },
];
export default {
  data() {
    return {
      Catelist: [],
      columns,
      pagination: {
        pageSizeOptions: ["5", "10", "20"],
        pageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `${total}`,
      },
      queryParam: {
        pagesize: 5,
        pagenum: 1,
      },
      CateInfo: {
        // 
        id: 0,
        name: "",
      },
      newCate: {
        // 
        id: 0,
        name: "",
      },
      visible: false, // 
      addCateVisible: false, // 
      CateRules: {
        // 
        name: [
          {
            validator: (rule, value, callback) => {
              if (this.CateInfo.name === "") {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
      },
      addCateRules: {
        // 
        name: [
          {
            validator: (rule, value, callback) => {
              if (this.newCate.name === "") {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
      },
      editCateVisible: false, // 
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get("category", {
        params: {
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
        },
      });
      if (res.status !== 200) return this.$message.error(res.message);
      this.Catelist = res.data;
      this.pagination.total = res.total;
      
    },
    // 
    handleTableChange(pagination, filters, sorter) {
      var pager = { ...this.pagination };
      pager.current = pagination.current;
      pager.pageSize = pagination.pageSize;
      this.queryParam.pagesize = pagination.pageSize;
      this.queryParam.pagenum = pagination.current;

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1;
        pager.current = 1;
      }
      this.pagination = pager;
      this.getCateList();
    },

    // 
    // async deleteCate(id) {
    //   const res = await this.$http.delete(`Cate/${id}`);
    //       if (res.status != 200) return this.$message.error(res.message);
    //       this.$message.success("");
    //       this.getCateList();
    //   // console.log(res)
    // },
    async deleteCate(id) {
      const res = await this.$http.delete(`category/${id}`);
      if (res.status != 200) return this.$message.error(res.message);
      this.getCateList();
      this.$message.success("");
      console.log(res);
    },
    cancel() {
      this.$message.error("");
    },

    // 
    addCateOk() {
      this.$refs.addCateRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        const { data: res } = await this.$http.post("category/add", {
          name: this.newCate.name,
        });
        if (res.status !== 200) return this.$message.error(res.message);

        this.addCateVisible = false;
        this.$message.success("");
        await this.getCateList();
      });
    },
    addCateCancel() {
      this.$refs.addCateRef.resetFields();
      this.addCateVisible = false;
      this.$message.info("");
    },

    // 
    async editCate(id) {
      this.editCateVisible = true;
      //   console.log(res+"++++++++++++++++++++++++++++++++++++++++++++++")
      const { data: res } = await this.$http.get(`category/${id}`);
      //   console.log(res+"----------------------------------------------")
      this.CateInfo = res.data;
      this.CateInfo.id = id;
    },
    editCateOk() {
      this.$refs.addCateRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        const { data: res } = await this.$http.put(
          `category/${this.CateInfo.id}`,
          {
            name: this.CateInfo.name,
          }
        );
        if (res.status != 200) return this.$message.error(res.message);

        this.editCateVisible = false;
        this.$message.success("");
        this.getCateList();
      });
    },
    editCateCancel() {
      this.$refs.addCateRef.resetFields();
      this.editCateVisible = false;
      this.$message.info("");
    },
  },
};
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>